import React from 'react';
import './App.css';
import EventList from './EventList/EventList'
import {useSelector, useDispatch} from 'react-redux'
import {getEvents, togglePopup} from './actions'
import CitySearchPopup from './CitySearchPopup/CitySearchPopup';
import RadiusPopup from './RadiusPopup/RadiusPopup';

function App() {
  const events = useSelector(state => state.updateEvents)
  const popup = useSelector(state => state.togglePopupReducer)
  const radius = useSelector(state => state.changeRadiusReducer)
  
  const dispatch = useDispatch()
  const defaultItem = {
    "city": "Lexington",
    "country": "us",
    "localized_country_name": "USA",
    "state": "MA",
    "name_string": "Lexington, Massachusetts, USA",
    "zip": "02421",
    "lat": 42.45,
    "lon": -71.23
    }
  function enterMeetup(item) {
    dispatch(getEvents(item))
  }

  function openClosePopup(whichPopup) {
    dispatch(togglePopup(whichPopup))
  }
  //TOGGLE WRAPPER BEHING POPUP
  let background;
  if(popup.open) {
    background = <div className="popup-background" id="popup-background" onClick={() => openClosePopup('')}></div>
  } else {
    background = null
  }

  return (
    <div className="App">
      {background}
      <nav className="top-menu">
        <ul>
          <li className="meetup-logo"></li>
        </ul>
      </nav>
      <div className="title-section">
        <h1 className="main-title">Find your next event</h1>
        <p className="subtitle-title">There are 104,845 Meetups happening this week about everthing from careers to hiking, <br></br> parenting, tech, photography and urban gardening...</p>
        <div className="black-search-section">
          <input className="event-searchbar" placeholder="Search" />
          <p className="filter-section">within <span onClick={() => openClosePopup('radius')} className="miles-filter">{radius.radius}</span> of <span onClick={() => openClosePopup('city')} className="city-filter">{events.city}</span></p>
          <CitySearchPopup />
          <RadiusPopup />
        </div>
      </div>
      <div className="event-area">
        <EventList events={events} />
      </div>
      <div onClick={() => enterMeetup(defaultItem)}>Start using App!</div>
    </div>
  );
}

export default App;
