import {combineReducers} from 'redux'

const getAPIEvents = (
    state = {
        loading: true,
        events: [],
        error: '',
        city: 'Stockholm, SE'
    }, 
    action) => {
    switch(action.type) {
        case 'FETCH_EVENTS_REQUEST':
            return {
                ...state,
                loading: true,
                city: `${action.payload.city}, ${action.payload.city}`
            }
        case 'FETCH_EVENTS_SUCCESS':
            return {
                ...state,
                loading: false,
                events: action.payload.events,
                error: '',
            }
        case 'FETCH_EVENTS_FAILURE':
            return {
                ...state,
                loading: false,
                events: [],
                error: action.payload,
            }
        default: return state
    }
}

const fetchAPISuggestions = (
    state = {
        loading: true,
        suggestions: [],
        error: ''
    }, 
    action) => {
    switch(action.type) {
        case 'FETCH_CITY_SUGGESTIONS_REQUEST':
            return {
                ...state,
                loading: true
            }
        case 'FETCH_CITY_SUGGESTIONS_SUCCESS':
            return {
                loading: false,
                suggestions: action.payload,
                error: ''
            }
        case 'FETCH_CITY_SUGGESTIONS_FAILURE':
            return {
                loading: false,
                suggestions: [],
                error: action.payload
            }
        case 'CLEAR_CITY_SUGGESTIONS':
            return {
                loading: false,
                suggestions: [],
                error: ''
            }
        default: return state
    }
}
const togglePopupReducer = (
    state = {
        open: false,
        popup: ''
    }, action) => {
    switch(action.type) {
        case 'TOGGLE_POPUP':
            return {
                open: !state.open,
                popup: action.payload
            }
        default: return state
    }
}
const changeRadiusReducer = (
    state = {
        radius: '2 miles'
    }, action) => {
        switch(action.type) {
            case 'CHANGE_RADIUS':
                return {
                    radius: action.payload
                }
            default: return state
        }
}

const counterReducer = (state = 0, action) => {
    switch(action.type) {
        case 'INCREMENT':
            return state + 1
        case 'DECREMENT':
            return state - 1
        default: return state
    }
}

const loggedReducer = (state = false, action) => {
    switch(action.type) {
        case 'SIGN_IN':
            return !state 
            default: return state   
    }
}

const rootReducer = combineReducers({
    counterReducer,
    loggedReducer,
    fetchAPISuggestions,
    getAPIEvents,
    togglePopupReducer ,
    changeRadiusReducer
})

export default rootReducer;